@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
*{

  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: muli;

}

.navbar-brand{
  font-size: 1.9rem !important;
  
}
.brand-name{
  color: brown !important;
}
.navbar a{
color: brown !important;
font-weight: bold;
text-transform: capitalize;

  
}

@media(max-width: 991px){
  .navbar-brand{
    font-size: 1.5rem !important;
  }
  .menu_active{
    font-weight: bold;
    border-bottom:none;
  }
}

/*main content*/

img{

  width: 100%;
  height: 400px;
}


/* main designing */
.main{
  width : 90%;
  height: auto;
  margin: 50px;
  padding: 50px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
  align-items: center;
  
  display: inline-flex;
}
  
.card{
  padding: 50px ;
  width : 15rem;
}

.main2{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  
}
.main3{
  display: grid;
  grid-template-rows: 1000px;
  grid-template-columns: 400px 400px;

}

.abimg{
  max-width: 100%;
  height : 100vh;
 background-attachment: fixed;
}

.con{
  display : grid;
 grid-template-rows: 400px;

}
.head1{
  text-align: center;
  font-weight: bold;
  font-style: italic;
  

}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

